@import "src/var";

.dropdown-btn {
	&:focus {
		outline: none;
		box-shadow: 0 0 0 0;
	}
	font-size: 16px;
	z-index: 600;
	.dropdown {
		width: calc(100% + 2px);
		padding-bottom: 5px;
		border-bottom-right-radius: 4px;
		border-bottom-left-radius: 4px;
		display: none;
		background: white;
		border: 1px solid $purple;
		left: -1px;
		top: calc(100% - 2px);
		z-index: 600;
		.item {
			padding: auto 15px;
			padding: 8px 0px;
			opacity: 0.7;
			transition: 150ms;
			&:hover {
				opacity: 1;
			}
		}
	}

	&.is-shown .dropdown {
		display: block;
	}

	.icon {
		display: inline-block;
		vertical-align: top;
		width: 50px;
		& > .view {
			right: 0;
			top: 4px;
			& > i {
				font-size: 26px;
			}
		}
	}
}
